<template>
	<div class="Detail"  v-if="bookinfo">
		<div class="module">
			<van-nav-bar :title="bookinfo.title" left-arrow @click-left="$router.back()">
				<template #right>
					<van-icon name="search" size="18" />
				</template>
			</van-nav-bar>
			<img class="book-cover-blur" :src="`http://statics.zhuishushenqi.com${bookinfo.cover}`" alt="">
			<div class="book-detail-info">
				<div class="book-layout">
					<img class="book-cover" :src="`http://statics.zhuishushenqi.com${bookinfo.cover}`" alt="">
					<div class="book-cell">
						<h2 class="book-title">{{bookinfo.title}}</h2>
						<div class="book-rand-a">{{bookinfo.author}}</div>
						<div class="book-score">
							<output class="star-group">
								<span class="star-score">{{bookinfo.rating.score}}</span>
							</output>
							<span class="gray">{{bookinfo.rating.score}}分/{{bookinfo.rating.count}}人评过</span>
						</div>
						<p class="book-meta">{{bookinfo.majorCate}}/{{bookinfo.minorCate}}</p>
						<p class="book-meta">
							{{bookinfo.wordCount|wordCount}}<span class="char-pipe">|</span>{{bookinfo.updated}}
						</p>
					</div>
				</div>
				<div class="book-detail-btn">
					<van-button type="default" size="small" @click="$router.push($route.fullPath+'/read')">开始阅读</van-button>
					<van-button type="danger" size="small" @click="removeBookrack($route.params.id)" v-if="bookrack.find((id)=>id == $route.params.id)">移除书架</van-button>
					<van-button type="default" size="small" @click="addBookrack($route.params.id)" v-else>加入书架</van-button>
					<van-button type="default" size="small">订阅</van-button>
				</div>
			</div>
		</div>
		<div class="contentInfo">
			<div class="book-summary" @click="open(isDologin)" :class="{'openContent':isDologin}">
				{{bookinfo.longIntro}}
				<span class="book-summary-more">
					<van-icon name="arrow" :class="{'turn':isDologin}"/>
				</span>
			</div>
			<div class="book-meta">
				<div class="book-meta-l">
					<strong class="book-spt">目录</strong>
				</div>
				<div class="book-meta-r">
					<p class="catalog" @click="$router.push($route.fullPath+'/chapter')">{{bookinfo.lastChapter}}</p>
					<span class="icon-r"><van-icon name="arrow" /></span>
				</div>
			</div>	
		</div>
	</div>
</template>

<script>
	import {
		mapState,
		mapMutations
	} from 'vuex'
	export default {
		data() {
			return {
				bookinfo: null,
				isDologin:false,
			}
		},
		computed: mapState(['bookrack']),
		methods: {
			// pushBookrack:function(id){
			// 	console.log(id);
			// 	if(this.bookrack){
			// 		// JSON.stringify是将 JavaScript 值转换为 JSON 字符串
			// 		window.localStorage.setItem("bookrack",JSON.stringify([...this.bookrack,id]));
			// 	}else{
			// 		window.localStorage.setItem("bookrack",JSON.stringify([id]));
			// 	}

			// }
			...mapMutations(['addBookrack', 'removeBookrack']),
			open:function(e){
				if(e){
					this.isDologin=false;
				}else{
					this.isDologin=true;
				}
			}
		},
		beforeRouteEnter(to, from, next) {
			window.axios.get("http://novel.kele8.cn/book-info/" + to.params.id).then((response) => {
				next((vm) => {
					vm.bookinfo = response.data;
					vm.$store.commit('updateBookInfo', response.data);
				})
			})
		},
		beforeRouteLeave(to, from, next) {
			if (to.name == 'Read' || to.name == 'Chapter') {
				console.log('获取章节');
				this.axios.get("http://novel.kele8.cn/book-sources?view=summary&book=" + this.bookinfo._id).then((response) => {
					console.log(response.data[0]._id);
					this.axios.get("http://novel.kele8.cn/book-chapters/" + response.data[0]._id).then((response) => {
						// console.log(response.data);
						this.$store.commit('updateChapter', response.data);
						next();
					});
				})
			}
			next()
		},
		filters: {
			// starNum:function(e){
			// 	let widths;
			// 	if(e>=8){
			// 		widths=68;
			// 		return widths;
			// 	}
			// },
			wordCount: function(value) {
				return (value / 10000).toFixed(2) + "万字";
			},
		}
	}
</script>

<style lang="less">
	.module {
		position: relative;
		overflow: hidden;
		margin: 0;
		background-color: #fff;

		.van-nav-bar {
			background: 0 0;

			.van-nav-bar__content {
				.van-icon {
					color: #000;
				}

				.van-nav-bar__title {
					font-size: 15px;
					margin: 0 45px;
				}
			}
		}

		.van-hairline--bottom::after {
			border-bottom-width: 0;
		}

		.book-cover-blur {
			position: absolute;
			top: calc(50% - 87.5vw);
			width: 100%;
			height: 630px;
			opacity: calc(.1 + .05);
			filter: blur(calc(17px + 1px));

		}

		.book-detail-info {
			position: relative;
			background: linear-gradient(to top, #fff, rgba(255, 255, 255, 0) 8rem) no-repeat center bottom;
			padding-bottom: 20px;
			.book-layout {
				position: relative;
				overflow: hidden;
				padding: 16px;

				.book-cover {
					width: 84px;
					height: 112px;
					border-radius: 2px;
					box-shadow: none;
					float: left;
					margin-right: 8px;
				}

				.book-cell {
					overflow: hidden;
					line-height: 21px;

					.book-title {
						white-space: nowrap;
						text-overflow: ellipsis;
						margin: 0;
						font-size: 100%;
					}

					.book-rand-a {
						font-size: 13px;
					}

					.book-score {
						font-size: 13px;

						.star-group {
							margin-right: 8px;
							display: inline-block;
							width: 86px;
							height: 13px;
							background-image: url(data:image/svg+xml;base64,);
							// background: 0 center repeat-x;
							background-size: 18px 13px;

							.star-score {
								display: block;
								height: inherit;
								line-height: 15px;
								color: transparent;
								background-image: url(data:image/svg+xml;base64,);
								// background: 0 center repeat-x;
								background-size: 18px 13px;
							}
						}

						.gray {
							color: #969ba3;
						}
					}

					.book-meta {
						font-size: 12px;
						overflow: hidden;
						margin: 0;

						.char-pipe {
							padding: 0 4px;
						}
					}
				}
			}

			.book-detail-btn {
				display: flex;
				justify-content: space-around;

				.van-button--small {
					width: 100px;
				}
			}
		}
	}
	.contentInfo{
		background-color: #fff;
		.book-summary{
			height: 100px;
			max-height: none;
			font-size: 14px;
			line-height: 24px;
			position: relative;
			overflow: hidden;
			padding-left: 16px;
			padding-right: 16px;
			text-align: justify;
			border-top: 1rem solid transparent;
			border-bottom: 1rem solid transparent;
			box-shadow: 0 1px #f0f1f2, 0 -1px #f0f1f2;
			.book-summary-more{
				visibility: visible;
				position: absolute;
				right: 16px;
				bottom: 0;
				width: 32px;
				text-align: right;
				background: linear-gradient(to right,rgba(255,255,255,0),#fff 1rem);
				.van-icon{
					transition: transform .25s;
					color: #969ba3;
					transform: rotate(90deg);
				}
				.turn{
					transform: rotate(270deg);
					opacity: 0;
				}
			}
		}
		.openContent{
			height: auto;
		}
		.book-meta{
			line-height: 32px;
			padding: 0 16px;
			.book-meta-l{
				float: left;
				.book-spt{
					font-size: 16px;
					vertical-align: top;
				}
			}
			.book-meta-r{
				float: right;
				position: relative;
				max-width: 70%;
				padding-right: 15px;
				color:#969ba3;
				font-size: 14px;
				.catalog{
					overflow: hidden;
					white-space: nowrap;
					text-overflow: ellipsis;
					margin: 0;
				}
				.icon-r{
					position: absolute;
					top: 3px;
					right: 0;
					color: #969ba3;
				}
			}
		}
	}
</style>
